import React, { useEffect } from "react";
import "./Home.css";
import InfoCardsContainer from "./InfoCards";
import ShopCategoryCards from "./ShopCategoryCards";
import HotInfo from "./HotInfo";

const MidHome = () => {
  useEffect(() => {
    fetch("data/info.json").then((response) => {
      response.json().then((data) => {
        console.log(data);
      });
    });
  }, []);

  const renderCirles = () => {
    let circles = [];
    for (let i = 0; i < 3; i++) {
      circles.push(<div key={i} className="circleDivider" />);
    }
    return circles;
  };

  return (
    <div className={"sectionContainer"}>
      <HotInfo />
      <InfoCardsContainer />

      <div style={{ paddingTop: "7rem", display: "flex"}}>
        <div
          style={{ width: "100%", display: "flex", justifyContent: "center" }}
        >
          {/*renderCirles()*/}
        </div>
        <img
          className={"vectorImg"}
          src={`/olio_tab.png`}
          alt="vector ciclo produzione olio"
        />
        <div
          style={{ width: "100%", display: "flex", justifyContent: "center" }}
        >
          {/*renderCirles()*/}
        </div>
      </div>
      <ShopCategoryCards/>
    </div>
  );
};

export default MidHome;
