import React from "react";
//styles
import "./Home.css";

const styles={
    sectionContainer:{ display: "flex", margin: "5rem 0px 0px 0px" }}

const HotInfo = () => {
  return (
    <div
      className={"sectionContainer"}
      style={styles.sectionContainer}
    >
      <div
        className={"infoCard"}
        style={{ padding: "0px 50px", width: "100%" }}
      >
        <div
          style={{
            backgroundImage: `linear-gradient( rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2) ),url("/info_banner.jpg")`,
            height: "300px",
            borderRadius: "1.25rem",
            backgroundSize: "cover"
          }}
        ></div>
      </div>
    </div>
  );
};

export default HotInfo;
