import React from "react";

import "./Home.css";
import InfoCard from "./InfoCard";
import { useNavigate } from "react-router-dom";
import { navigateTo } from "../../utils";

const InfoCards = () => {
  const navigate = useNavigate();
  
  const goToAboutUs=()=>{
    navigateTo(navigate,"/AboutUs")
  }

  const goToArtPage=()=>{
    console.log("artPage")
    //navigate("/AboutUs")
  }
  return (
    <div className={"sectionContainer"}>
      <div className="catalogHeaderContainer" style={{margin:"5rem 50px 0.5rem 50px"}}>
        <h2 className={"catalogHeaderTitle"}>About Us</h2>
      </div>

      <div className={"cardsContainer cardsContainerMedium"}>
        <div className={"infoCard"} style={{ padding: "0px 0px 0px 50px" }}>
          <InfoCard
            image={"/card_1.jpg"}
            text={"Chi Siamo"}
            margin={"0px 7px 0px 0px"}
            btnCallback={goToAboutUs}
          />
        </div>
        <div className={"infoCard"} style={{ padding: "0px 50px 0px 0px" }}>
          <InfoCard
            image={"/card_2.jpeg"}
            text={`"Arte" & Olio`}
            margin={"0px 0px 0px 7px"}
            btnCallback={goToArtPage}
          />
        </div>
      </div>
    </div>
  );
};

export default InfoCards;
