import React from "react";
import ProductCard from "../shop/ProductsCard";

//styles
import "./Home.css";
import GoToStoreComponent from "./GoToStoreComponent";
import { useSelector } from "react-redux";
import { SIZES_LABEL } from "../../constants";

const CatalogHome = () => {
  const { products } = useSelector((state) => state.products);

  const renderHomeCatalog = () => {
    let cards = [];
    let counter = 0;
    for (let productType of products) {
      for (let product of productType) {
        if (product.isHot) {
          cards.push(
            <ProductCard
              isAvailable={product.isAvailable}
              enableDisplay={false}
              stocksNum={product.stoks}
              isHome={true}
              type={product.type}
              key={"card_" + counter}
              title={product.name}
              price={product.price}
              bgImage={product.image}
              size={SIZES_LABEL[product.size]}
            />
          );
          counter++;
        }
      }
    }

    /*for (let i = 0; i <= 3; i++) {
      cards.push(<ProductCard key={i} title={"Titolo"} price={"10"} bgImage={`/shop_card_768x768.jpg`}/>);
    }*/
    return cards;
  };

  return (
    <div className={"sectionContainer"} style={{ width: "100%" }}>
      <div className={"catalogHeaderContainer"}>
        <div style={{ display: "flex" }}>
          <h2 className={"catalogHeaderTitle"}>Prodotti Hot</h2>
          <img
            className={"catalogTitleEmoji"}
            src={`/icons8-fire-100.png`}
            alt="fire icon"
          />
        </div>
        <GoToStoreComponent />
      </div>

      <div className={"catalogContainer"}>{renderHomeCatalog()}</div>
    </div>
  );
};

export default CatalogHome;
