import React from "react";

import "./Home.css";
import { Button, styled } from "@mui/material";

const CustomButton = styled(Button)({
  color: "#1E1E1E",
  fontWeight: "400",
  borderRadius: "12px",
  boxShadow: "none",
  textTransform: "none",
  fontSize: 15,
  padding: ".75rem 1rem",
  border: "none",
  lineHeight: "1rem",
  backgroundColor: "#fff",
  borderColor: "#000",
  justifyContent: "center",
  display: "inline-flex",
  fontFamily: [
    "-apple-system",
    "BlinkMacSystemFont",
    '"Segoe UI"',
    "Roboto",
    '"Helvetica Neue"',
    "Arial",
    "sans-serif",
    '"Apple Color Emoji"',
    '"Segoe UI Emoji"',
    '"Segoe UI Symbol"',
  ].join(","),
  "&:hover": {
    border: "none",
    backgroundColor: "#dadada",
    boxShadow: "none",
  },
  "&:active": {
    boxShadow: "none",
    backgroundColor: "#000",
    borderColor: "#000",
  },
});

const InfoCard = ({image,text,margin="8px",btnCallback}) => {
  
  return (
    <div
      className={"cardImage"}
      style={{ backgroundImage: `linear-gradient( rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2) ),url("${image}")`,margin }}
    >
      <div className={"cardContentContainer"}>
        <p
          style={{
            padding: "0.25rem 0px",
            color: "#fff",
            fontWeight: "800",
            fontSize: "1.15rem",
          }}
        >
          {text}
        </p>
        <CustomButton onClick={btnCallback} variant="outlined">Scopri di più</CustomButton>
      </div>
    </div>
  );
};

export default InfoCard;
