import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import Checkbox from "@mui/material/Checkbox";
import ProductCard from "./ProductsCard";
import MultiRangeSlider from "./MultiRangeSlider";
import { setFilters } from "../../Reducers/filters";
//const
import { CATEGORIES, SIZES_LABEL } from "../../constants";
//styles
import "./Shop.css";

const SIZES = {
  0: "0.2",
  1: "0.5",
  2: "5",
};

const MIN_PRICE = 10;
const MAX_PRICE = 22;

const Shop = () => {
  const dispatch = useDispatch();

  const productsList = useSelector((state) => state.products);
  const { filters } = useSelector((state) => state.filters);

  const [priceRange, setPriceRange] = useState([MIN_PRICE, MAX_PRICE]);

  const checkExistence = (arr, target) => {
    let index = arr.indexOf(target.id);
    if (target.checked && index === -1) {
      arr.push(target.id);
    } else if (!target.checked && index !== -1) {
      arr.splice(index, 1);
    }
    return arr;
  };

  const handleOnCheckedType = (e) => {
    dispatch(
      setFilters({ types: checkExistence([...filters.types], e.target) })
    );
  };

  const handleOnCheckedSize = (e) => {
    dispatch(setFilters({ size: checkExistence([...filters.size], e.target) }));
  };

  const handleOnCheckedAvailability = (e) => {
    dispatch(setFilters({ isAvailable: e.target.checked }));
  };

  const renderFilterForm = () => {
    return (
      <div className={"filterPanelContainer"}>
        <h4>Filtra Per Tipo</h4>
        <div className={"filterPanelPropsText"}>
          <Checkbox
            id={CATEGORIES[0]}
            onChange={handleOnCheckedType}
            checked={filters.types.includes(CATEGORIES[0])}
          />
          Ravece
        </div>
        <div className={"filterPanelPropsText"}>
          <Checkbox
            id={CATEGORIES[1]}
            onChange={handleOnCheckedType}
            checked={filters.types.includes(CATEGORIES[1])}
          />
          Leccino
        </div>
        <div className={"filterPanelPropsText"}>
          <Checkbox
            id={CATEGORIES[2]}
            onChange={handleOnCheckedType}
            checked={filters.types.includes(CATEGORIES[2])}
          />
          Multicultivar
        </div>
        <h4>Filtra Per Size</h4>
        <div className={"filterPanelPropsText"}>
          <Checkbox id={SIZES[0]} onChange={handleOnCheckedSize} />
          250Ml.
        </div>
        <div className={"filterPanelPropsText"}>
          <Checkbox id={SIZES[1]} onChange={handleOnCheckedSize} />
          500Ml.
        </div>
        <div className={"filterPanelPropsText"}>
          <Checkbox id={SIZES[2]} onChange={handleOnCheckedSize} />
          5L.
        </div>
        <h4>Filtra Per Disponibilità</h4>
        <div className={"filterPanelPropsText"}>
          <Checkbox onChange={handleOnCheckedAvailability} />
          Disponibile
        </div>
        <h4>Prezzo MIN-MAX</h4>
        <div>
          <MultiRangeSlider
            min={MIN_PRICE}
            max={MAX_PRICE}
            onChange={({ min, max }) => {
              if(min === priceRange[0] && max === priceRange[1])return
              setPriceRange([min, max])}
            }
          />
        </div>
      </div>
    );
  };

  const renderShopCards = (cardList) => {
    let sectionCards = [];
    for (let i = 0; i < cardList.length; i++) {
      if (
        (filters.size.length === 0 ||
          filters.size.includes(cardList[i].size)) &&
        (!filters.isAvailable || cardList[i].stoks > 0) &&
        cardList[i].price >= priceRange[0] &&
        cardList[i].price <= priceRange[1]
      ) {
        sectionCards.push(
          <ProductCard
            isAvailable={cardList[i].isAvailable}
            key={"card_" + i}
            enableDisplay={false}
            title={cardList[i].name}
            size={SIZES_LABEL[cardList[i].size]}
            price={cardList[i].price}
            stocksNum={cardList[i].stoks}
            bgImage={cardList[i].image}
            isHome={false}
            type={cardList[i].type}
          />
        );
      }
    }

    if(sectionCards.length === 0) sectionCards = <div style={{margin: "1rem"}}>{"NESSUN PRODOTTO DISPONIBILE"}</div>

    return sectionCards;
  };

  const renderShopDetails = () => {
    let details = [];
    for (let i = 0; i < productsList.products.length; i++) {
      if (filters.types.length === 0 || filters.types.includes(CATEGORIES[i])) {
        details.push(
          <div key={i} id={"section_" + CATEGORIES[i]}>
            <div className={"categoryTitleContainer"}>
              <h2 className={"categoryTitle"}>{CATEGORIES[i]}</h2>
              <div className={"separator"} />
            </div>
            <div id={"catalogShopContainer"} className={"catalogShopContainer"}>
              {renderShopCards(productsList.products[i])}
            </div>
          </div>
        );
      }
    }
    return details;
  };
  return (
    <div className={"shopContainer"}>
      <div className={"filterPanel"}>{renderFilterForm()}</div>
      <div className={"productsPanel"}>{renderShopDetails()}</div>
    </div>
  );
};
export default Shop;
