import React from "react";

//styles
import "./Home.css";
import BlogCard from "../Blog/BlogCard";

const BlogHome = () => {
  const renderHomeCatalog = () => {
    let cards = [];
    for (let i = 0; i < 3; i++) {
      cards.push(<BlogCard key={i} />);
    }
    return cards;
  };

  return (
    <div className={"sectionContainer"} style={{ width: "100%" }}>
      <div className={"catalogHeaderContainer"}>
        <div style={{ display: "flex" }}>
          <h2 className={"catalogHeaderTitle"}>"Ultime" dal Blog</h2>
          <img
            className={"catalogTitleEmoji"}
            src={`/icons8-fire-100.png`}
            alt="fire icon"
          />
        </div>
        <div>Vai al Blog</div>
      </div>

      <div className={"catalogContainer"}>{renderHomeCatalog()}</div>
    </div>
  );
};

export default BlogHome;
