import React, { useState } from "react";
import { IconButton } from "@mui/material";

//icons
import ShoppingCartIcon from "@mui/icons-material/ShoppingCart";
//styles
import "./Shop.css";
import { navigateTo } from "../../utils";
import { useNavigate } from "react-router-dom";
import { useDispatch } from "react-redux";
import { setCurrentSelected } from "../../Reducers/product";

const styles = {
  stockStyling: {
    padding: "0px 1rem",
    fontSize: "0.9rem",
    lineHeight: "inherit",
    fontWeight: "500",
  },
};

const ProductCard = ({
  enableDisplay = true,
  title,
  price,
  stocksNum,
  bgImage,
  type,
  isHome = true,
  size
}) => {
  const navigate = useNavigate();
  const dispatch = useDispatch()

  const [displayCart, setDisplayCart] = useState(false);
  const [isSelected, setIsSelected] = useState(false);

  //handles on click cart button
  const handleOnClick = () => {
    setIsSelected(!isSelected);
  };

  const handleIconMouse = (enable) => {
    if (enableDisplay) setDisplayCart(enable);
  };

  const handleOnProductClicked = () => {
    dispatch(setCurrentSelected({
      title,
      price,
      stocksNum,
      bgImage,
      type,
      size
    }))
    navigateTo(navigate, "/ProductPage");
  };

  return (
    <div
      className={"shopProductCardContainer"}
      onMouseEnter={() => handleIconMouse(true)}
      onMouseLeave={() => handleIconMouse(false)}
      onClick={handleOnProductClicked}
    >
      <div className="imageProduct">
        {stocksNum === 0 && (
          <img
            className={"emojiStyling"}
            src={`/Sold Out.png`}
            alt="unavailable emoji"
          />
        )}
        <img
          className={"shopCardImage"}
          src={bgImage}
          alt="vector ciclo produzione olio"
        />
      </div>

      <div className={"shopCartContainer"}>
        <div className={"shopCardInfoContainer"}>
          <h2 className={"shopCardTitle"}>{`${title} - ${size}`}</h2>
          <div className="shopCardInfo">
            <p className={"shopCardPrice"}>{`€${price}`}</p>
            {!isHome && stocksNum > 0 && (
              <div className={"shopCardPrice"}>
                <p
                  style={{ ...styles.stockStyling }}
                >{`Disponibili: ${stocksNum}`}</p>
              </div>
            )}
          </div>
        </div>
        {displayCart && (
          <IconButton className={"btnCart"} onClick={handleOnClick}>
            {isSelected ? "V" : <ShoppingCartIcon />}
          </IconButton>
        )}
      </div>
    </div>
  );
};

export default ProductCard;
