import React from "react";
import { useNavigate } from "react-router-dom";
import { useDispatch } from "react-redux";
import { navigateTo } from "../../utils";
import InfoCard from "./InfoCard";
import GoToStoreComponent from "./GoToStoreComponent";

import "./Home.css";
import { CATEGORIES } from "../../constants";
import { setFilters } from "../../Reducers/filters";

const ShopCategoryCards = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();

  return (
    <div className={"sectionContainer"}>
      <div className={"catalogHeaderContainer"}>
        <h2 className={"catalogHeaderTitle"}>Shop by Category</h2>
        <GoToStoreComponent />
      </div>

      <div className={"cardsContainer cardsContainerLarge"}>
        <div className={"infoCard"} style={{ padding: "0px 0px 0px 50px" }}>
          <InfoCard
            btnCallback={() => {
              dispatch(setFilters({ types:[CATEGORIES[0]]}))
              navigateTo(navigate, "/Shop");
            }}
            image={"/full_bottle.jpg"}
            text={`Monocultivar "Ravece"`}
            margin={"0px 7px 0px 0px"}
          />
        </div>
        <div
          className={"infoCard infoCardColumn"}
          style={{ padding: "0px 50px 0px 0px" }}
        >
          <div style={{ height: "calc(50% - 7px)" }}>
            <InfoCard
              btnCallback={() => {
                dispatch(setFilters({ types:[CATEGORIES[1]]}))
                navigateTo(navigate, "/Shop");
              }}
              image={"/full_bottle01.jpg"}
              text={`Monocultivar "Leccino"`}
              margin={"0px 0px 0px 7px"}
            />
          </div>
          <div style={{ height: "calc(50% - 7px)" }}>
            <InfoCard
              btnCallback={() => {
                dispatch(setFilters({ types:[CATEGORIES[2]]}))
                navigateTo(navigate, "/Shop");
              }}
              image={"/full_bottle02.png"}
              text={`Olio extravergine "Misto"`}
              margin={"0px 0px 0px 7px"}
            />
          </div>
        </div>
      </div>
    </div>
  );
};

export default ShopCategoryCards;
