import React from "react";

//styles
import "./infoPages.css";
import { Card, CardContent, Typography } from "@mui/material";

const CARDS_CONTENT = [
  {
    title: "Passione",
    text: "L'azienda è guidata dalla passione per l'olivicoltura e dal rispetto per le piante. I componenti del gruppo, difatti, nutrono da sempre una magica ammirazione verso le caratteristiche dell’ulivo, la cui resilienza e maestosità assumono un fascino ancestrale ai nostri occhi.",
  },
  {
    title: "Condivisione",
    text: "Olio Ruocco nasce e viene gestito a livello familiare. Al suo interno i vari componenti collaborano e si confrontano per cercare di arricchire l’intero progetto con le proprie impressioni e conoscenze. La condivisione si consuma, dunque, sia nella fase di ideazione che in quella pratica del lavoro quotidiano.",
  },
  {
    title: "Autenticità",
    text: "Il nostro progetto vuole mantenere una forma di coerenza con il territorio in cui si inserisce. La montagna, infatti, non sa dire bugie. Il nostro desiderio è quello di custodire questa sensazione in ogni goccia del nostro Olio e trasmetterla alla tavola del consumatore finale.",
  },
  {
    title: "Creatività",
    text: "Questa è il faro di tutte le nostre azioni. Il nostro fine è quello di offrire al cliente il miglior prodotto possibile. Nel farlo, però, vogliamo goderci l’intero processo e non rinunciare mai alla componente divertimento. Per far si che ciò possa tramutarsi in realtà è necessario che la voglia di innovare ed adottare un approccio creativo non si spenga.",
  },
  {
    title: "Sostenibilità",
    text: "Il nostro Olio nasce dalla natura e pertanto di essa vuole avere rispetto. In linea con questa visione abbiamo deciso di seguire la strada del Biologico per la fase di produzione. Gli interventi in campo sono quindi limitati e condotti solo con prodotti consentiti dalle normative. A tutela del consumatore e dell’ambiente la nostra azienda, con scadenze periodiche, è sottoposta a controllo da parte di un ente qualificato.",
  },
];

const OurValuesCards = () => {
  const renderCards = () => {
    let cards = [];
    for (let i = 0; i < CARDS_CONTENT.length; i++) {
      cards.push(
        <Card key={i} sx={{ width: 345,height:300,margin:"2rem 5rem"}}>
          <CardContent>
            <Typography gutterBottom variant="h5" component="div">
              {CARDS_CONTENT[i].title}
            </Typography>
            <Typography variant="body2" sx={{ color: "text.secondary" }}>
              {CARDS_CONTENT[i].text}
            </Typography>
          </CardContent>
        </Card>
      );
    }
    return cards;
  };
  return <>{renderCards()}</>;
};
export default OurValuesCards;
