import React, { useState } from "react";
import { IconButton } from "@mui/material";

//styles
import "./Blog.css";

const BlogCard = () => {
  const [isSelected, setIsSelected] = useState(false);

  //handles on click cart button
  const handleOnClick = () => {
    setIsSelected(!isSelected);
  };

  return (
    <div
      className={"blogProductCardContainer"}
    >
      <img
        className={"blogCardImage"}
        src={`/blog_home_test768x432.jpeg`}
        alt="vector ciclo produzione olio"
      />
      {<div className={"blogCartContainer"}>
        <div className={"blogCardInfoContainer"}>
          <h3 className={"blogCardTitle"}>Titolo Articolo</h3>
        </div>
      </div>}
    </div>
  );
};

export default BlogCard;
