import React from "react";
import ImageZoom from "react-image-zooom";

import "./Shop.css";

import { useSelector } from "react-redux";
import { Carousel } from "react-responsive-carousel";

import "react-responsive-carousel/lib/styles/carousel.min.css";

const styles = {
  separator_styling: { marginTop: "1rem" },
  p_container_styling: {
    display: "flex",
    flexDirection: "row",
    alignItems: "baseline",
    lineHeight: "4rem",
  },
  price_styling: { fontSize: "1.5rem", lineHeight: "2rem" },
  key_styling: { fontWeight: 600, fontSize: "large" },
  text_styling: { fontSize: "large", margin: "0rem 0.5rem" },
  thumb_img_styling: {
    maxWidth: "12vw",
    maxHeight: "12vw",
    marginLeft: "1rem",
    borderRadius: "0.5rem",
  },
  thumb_img_container_styling: { margin: "2rem 0rem", dispay: "flex" },
};

const ProductPage = () => {
  const { currentSelected } = useSelector((state) => state.products);
  let imagePaths = [
    currentSelected.bgImage,
    `/${currentSelected.type}/label_${currentSelected.title} - ${currentSelected.size}.png`,
    `/${currentSelected.type}/label_${currentSelected.title} - ${currentSelected.size}_b.png`,
  ];

  const renderSliderContent = () => {
    let images = [];
    for (let [i, imgPath] of imagePaths.entries()) {
      images.push(
        <div key={i}>
          <ImageZoom
            className={"productPageMainImg"}
            src={imgPath}
            alt="product page main Image"
            zoom="200"
          />
        </div>
      );
    }
    return images;
  };

  const renderCustomThumbs = (children) => {
    return children.map((child, i) => (
      <img
        key={i}
        src={child.props.children.props.src}
        alt=""
      />
    ));
  };

  return (
    <div className="productPage">
      <div className="productPageContainer">
        <Carousel
          showArrows={false}
          showStatus={false}
          showIndicators={true}
          showThumbs={true}
          autoPlay={false}
          transitionTime={500}
          renderThumbs={renderCustomThumbs}
          className={"productPageMainImg"}
          thumbWidth={150}
        >
          {renderSliderContent()}
        </Carousel>

        <div className={"productPageContentContainer"}>
          <div className={"productPageTitle"}>
            {`${currentSelected.title} ${currentSelected.type}`}
          </div>
          <div style={styles.separator_styling} className={"separator"} />
          <p style={styles.price_styling}>{`€${currentSelected.price}`}</p>
          <div style={styles.p_container_styling}>
            <p style={styles.key_styling}>Dimensioni:</p>
            <p style={styles.text_styling}>{currentSelected.size}</p>
          </div>
          <p style={styles.key_styling}>Descrizione:</p>
        </div>
      </div>
    </div>
  );
};

export default ProductPage;
